import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Location as AngularLocation}                 from '@angular/common';
import { Location, LocationsService } from "../../../generated/ports-locations/index";
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'app-location-detail',
  templateUrl: './location-detail.component.html',
  styleUrls: ['./location-detail.component.css']
})
export class LocationDetailComponent implements OnInit {

  ngOnInit(): void {
    this.route.paramMap
      .switchMap((params: ParamMap) => this.locationsService.getLocationUsingGET(params.get('id')))
      .subscribe(port => this.port = port);
  }
  constructor(
    private route: ActivatedRoute,
    private angularLocation: AngularLocation,
    private locationsService: LocationsService
  ) { }
  goBack(): void {
    this.angularLocation.back();
  }
  save(): void {
    //
  }
  @Input() port: Location;
}



