import { Component, OnInit } from '@angular/core';
import { PortService, Port } from "../../../generated/ports/index";
import { LocationsService, Location, DistancesService } from "../../../generated/ports-locations/index";
import { Router } from "@angular/router";

@Component({
  selector: 'app-ports',
  templateUrl: './ports.component.html',
  styleUrls: ['./ports.component.css'],
  providers: [PortService]
})

export class PortsComponent implements OnInit {

  private ports: Port[];
  private rowHeaders: string[];
  private colHeaders: string[];
  private options: any;
  private locations: Location[] = new Array();
  private columns: any[];
  ngOnInit(): void {
    this.getPorts();
  }

  constructor(
    private router: Router,
    private portService: PortService,
    private locationsService: LocationsService,
    private distancesService: DistancesService
  ) { }

  getPorts(): void {
   this.portService.fetchAllUsingGET().toPromise()
    .then(ports => { 
      this.ports = ports;
      this.distancesService.getVersionsUsingGET().toPromise()
        .then(versions => {
          if (versions.length > 0) {
            this.locationsService.getLocationsUsingGET(versions[0].identifier).toPromise()
              .then(locations => {
                this.locations = locations;
                this.createSpreadsheet();
              })
              .catch(error => {
                console.log(error)
              });
          }
        })
        .catch(error => {
          console.log(error)
        });
    })
    .catch(error => { 
      console.log(error);
      this.ports = [];
      this.createSpreadsheet();
  });
  }

  createEmptyPort(): Port {
    const port: Port= {'mmxId': '',
      'locationMmxId': '',
      'defaultStartTime': null,
      'allowCooldown': null,
      'berths': null,
      'capabilities': [],
      'cvValue': null,
      'dischargeDuration': null,
      'loadDuration': null,
      'maxCvValue': null,
      'minCvValue': null,
  }

    return port;
  }


  createSpreadsheet(): void {
    let source = this.locations
                    .filter(l => l.virtual == false)
                    .map(l => {
                      let res = [];
                      res.push(l.name);
                      res.push(...l.aliases);
                      return res; })
                    .reduce((a, b) => a.concat(b), []);
    
    this.columns= [
        {data: "locationMmxId", title: 'Location Id', type: 'autocomplete',
          source: source,
          strict: true,
          allowInvalid: false,
          width: 180}, 
        {data: 'defaultStartTime', title: 'Start Time', type: 'time', timeFormat: 'h:mm', correctFormat: true},
        {data: 'dischargeDuration', title: 'Discharge duration (hrs)', type: 'numeric'},
        {data: 'loadDuration', title: 'Load duration (hrs)', type: 'numeric'},
        {data: 'cvValue', title: 'CV (mmBTU)', type: 'numeric'},
        {data: 'maxCvValue', title: 'Max CV (mmBTU)', type: 'numeric'},
        {data: 'minCvValue', title: 'Min CV (mmBTU)', type: 'numeric'},
        {data: 'allowCooldown', title: 'Allow Cooldown', type: 'checkbox'},
        {data: 'berths', title: 'Berths', type: 'numeric'},
        {data: 'capabilities', title: 'Capabilities', type: 'dropdown', source: ['LOAD', 'DISCHARGE', 'DRYDOCK', 'MAINTENANCE']},
    ];


    this.rowHeaders = [];
    this.rowHeaders = this.ports.map(p => p.mmxId);
    
    this.options = {
      data: this.ports,
      rowHeaders: this.rowHeaders,
      colHeaders: this.colHeaders,
      columns: this.columns,
      rowHeaderWidth: 150,
      autoColumnSize: true,
      manualColumnMove: true,
      manualRowMove: true,
      manualColumnResize: true,
      bindRowsWithHeaders: true,
      manualRowResize: true,
      minSpareRows: 1,
      contextMenu: true,
      search: true,
      columnSorting: true,
      sortIndicator: true,
      undo: true,
    }
  }
  
  isEmpty(obj): boolean {
    return Object.values(obj).every(x => {return (x == false || x === "" || x == null)});
  }

  savePorts(): void {
    let ports = this.ports.map((p) => {
      return Object.assign(this.createEmptyPort(), p);
    });
    
    ports = ports.filter((p) => {
      return !this.isEmpty(p);
    });
    console.log(ports);

    this.portService.bulkInsertUsingPOST(ports).toPromise()
    .then(() => {console.log('Ok !')})
    .catch(() => {console.log('Not Ok!')});
  }
}



