import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ActivatedRoute, Params } from '@angular/router';

@Component({
 selector: 'my-app',
  template: `
    <div *ngIf="showNavHeader">
      <h1>{{title}}</h1>
      <nav>
          <a routerLink="/distances" routerLinkActive="active">Distances</a>
          <a routerLink="/ports" routerLinkActive="active">Ports</a>
          <a routerLink="/pricing/latest" routerLinkActive="active">Pricing</a>
          <a routerLink="/vessels/latest" routerLinkActive="active">Vessels</a>
      </nav>
    </div>
    <router-outlet></router-outlet>
 `,
 styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'Data Navigator';
  showNavHeader = true;

  ngOnInit() {
    // subscribe to router event
    console.log(window.location.href)
    if (window.location.href.includes('apiBaseUrl')) {
      this.showNavHeader = false;
    }
  }


}



