import { Component, OnInit } from '@angular/core';
import { LocationsService, Location } from "../../../generated/ports-locations/index";
import { Router } from "@angular/router";

@Component({
  selector: 'app-locations',
  templateUrl: './locations.component.html',
  styleUrls: ['./locations.component.css'],
  providers: [LocationsService]
})
export class LocationsComponent implements OnInit {

  ngOnInit(): void {
    this.getPorts();
  }
  constructor(
    private router: Router,
    private portsService: LocationsService
  ) { }
  ports: Location[];

  getPorts(): void {
    this.portsService.getLocationsUsingGET().toPromise()
      .then(ports => this.ports = ports)
      .catch(error => console.log(error));
  }

  gotoDetail(selectedLocation: Location): void {
    this.router.navigate(['/locations', selectedLocation.mmxId]);
  }
}



