import { Component, OnInit } from '@angular/core';
import { VesselStateAttributesComponent } from './vessel-state-attributes/vessel-state-attributes.component';
import { VesselsService, Vessel, Version, FuelConsumption, VesselStateAttributes } from "../../../generated/vessels/index";
import { ActivatedRoute, Params } from '@angular/router';


@Component({
  selector: 'app-vessel',
  templateUrl: './vessel.component.html',
  styleUrls: ['./vessel.component.css'],
  providers: [VesselsService]
})

export class VesselComponent implements OnInit {
  mmxId: string;
  versionNumber: number;

  constructor(private activatedRoute: ActivatedRoute, private vesselsService: VesselsService) {
  }

  version : Version = {
    identifier: '',
    vessels: null
  }

  fuelConsumption : FuelConsumption[] = [{
    consumption: 0,
    speed: 0
  }];

  ladenAttributes : VesselStateAttributes = {
    fuelConsumption: this.fuelConsumption,
    idleBaseRate: 0,
    idleNBORate: 0,
    inPortBaseRate: 0,
    inPortNBORate: 0,
    nboRate: 0,
    serviceSped: 0
  }
  
  ballastAttributes : VesselStateAttributes = {
    fuelConsumption: this.fuelConsumption,
    idleBaseRate: 0,
    idleNBORate: 0,
    inPortBaseRate: 0,
    inPortNBORate: 0,
    nboRate: 0,
    serviceSped: 0
  }
  
  vessel: Vessel = {
    ballastAttributes: this.ballastAttributes,
    ladenAttributes: this.ladenAttributes,
    baseFuel: 'HFO',
    capacity: 0,
    coolingVolume: 0,
    fillCapacity: 0,
    hasReliqCapacity: false,
    imo: '1',
    inaccessiblePorts: new Array<string>(),
    inaccessibleRoutes: new Array<string>(),
    maxSpeed: 0,
    minBaseFuelConsumption: 0,
    minHeel: 0,
    minSpeed: 0,
    mmxId: '',
    name: '',
    pilotLightRate: 0,
    scnt: 0,
    warmingTime: 0
  };

  ngOnInit() {
   // subscribe to router event
    this.activatedRoute.params.subscribe((params: Params) => {
      let vesselId = params['version'];
      if (vesselId) {
        this.getVessel(vesselId);
      }
      });
  }
  
  getVessel(mmxId: string): void {
    this.vesselsService.getVesselUsingGET(mmxId).toPromise()
      .then(vessel => this.vessel = vessel)
      .catch(error => console.log(error));
  }

  logForm(): void {
    this.vesselsService.insertVesselUsingPOST(this.vessel).toPromise()
      .then(vessel => vessel)
      .catch(error => console.log(error));
  }
}



